export default {
	props: {
		good: Object,
		isgood: {
			type: Boolean,
			default: false
		},
		SEGoods:Array
	},
	created() {
		console.log('是否是商城', this.isgood);
		console.log(JSON.stringify(this.SEGoods));
	},
	onLoad() {
		// console.log(this.good.volume);
		console.log(JSON.stringify(this.SEGoods));
	},
	computed: {
		jhj(){
			console.log(JSON.stringify(this.good));
			return (this.good.zk_final_price-this.good.youhuiquan).toFixed(2);
		},
		fontSize() {
			return `${this.size}px`
		},
		logo() {
			return this.good.user_type == 0 ? '../../static/tb.png' : '../../static/tm.png';
		},
		yj() {
			var v=this.good.youhuiquan * (parseFloat(this.good.commission_rate / 100))/100
			return v.toFixed(2);
		}
	},
	methods: {
		onTap() {
			this.$emit('onTap')
		}
	}
}
